import React from 'react';

class CapitalCities extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			cities: [ 'london', 'new york', 'berlin', 'paris' ]
		};
	}

	render() {
		return (
			<div>
				{this.state.cities.map(city => {
					return <p>{city}</p>;
				})}
			</div>
		);
	}
}

export default CapitalCities;
